html {
  --theme-white: #f9f9f9;
  --theme-gray: #5a6177;
  --theme-yellow: #ffbf00;
  --theme-purple: #9a66ff;
  --theme-blue: #4c97ff;
  --theme-magenta: #cf63cf;
  --theme-orange: #ffab1a;
  --theme-cyan: #5bb1d6;
  --theme-green: #58c059;
  --theme-fire: #ff8c19;
  --theme-red: #ff6619;
  --theme-pink: #ff6680;
  --app-icon-blue: #36a6ff;
  --block-literal-editor-button-width: 120px;
}

body {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--theme-white);
}

#GhostDiv {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

#GhostSvg {
  position: absolute;
  top: 0;
  left: 0;
}

#GhostHider {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  border: 0;
  padding: 0;
  background-color: var(--theme-white);
}

.App,
.AppMain,
.PleaseSwitchToLandscapeRequestContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  margin: 0;
  border: 0;
  padding: 0;
}

.AppMain,
.PleaseSwitchToLandscapeRequestContainer {
  z-index: 2;
}

#FrontCanvas {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  margin: 0;
  border: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

.PleaseSwitchToLandscapeRequestContainer {
  text-align: center;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  font-size: 40px;
  font-family: sans-serif;
  background-color: var(--app-icon-blue);
  color: var(--theme-white);
}

.ManualBreak__Segment {
  display: inline-block;
}

.MainSvg {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  border: 0;
  padding: 0;
}

#BlockLiteralEditor {
  position: absolute;
  z-index: 4;
  --width: 80vw;
  --height: 50px;
  width: var(--width);
  height: var(--height);
  top: calc(25vh - var(--height) / 2);
  left: calc(50vw - var(--width) / 2);
  background-color: var(--theme-white);
  border: 1px solid var(--theme-gray);
  border-radius: 5px;
  box-shadow: 0 0 10vh 1vh black;
  margin: 0;
  padding: 0;
}

#BlockLiteralEditor__Input {
  position: absolute;
  top: 0;
  left: 0;
  --padding: 16px;
  padding: var(--padding);
  height: calc(100% - 2 * var(--padding) - 2px);
  width: calc(
    100% - var(--block-literal-editor-button-width) - 2 * var(--padding)
  );
  margin: 0;
  border: none;
  outline: none;
  border-radius: 4px 0 0 4px;
  font-size: 16px;
}

#BlockLiteralEditor__Input::selection {
  background: black;
  color: white;
}

#BlockLiteralEditor__Button {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: var(--block-literal-editor-button-width);
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
  border-radius: 0 4px 4px 0;
  font-size: 16px;
  background-color: var(--theme-purple);
  color: var(--theme-white);
  cursor: pointer;
}

.Hidden {
  display: none;
}
